
/***引入mint-ui***/
import { Toast, Indicator, MessageBox } from 'mint-ui';
import $ from 'jquery';
export default {

  /***提示语***/
  toast(val) {
    let instance = Toast(val);
    setTimeout(() => {
      instance.close();
    }, 1000);
  },
  /***提示框***/
  messagebox(val) {
    return MessageBox.confirm(val, '提示')
  },
  /***手机号验证***/
  isPoneAvailable(val) {
    var myreg = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/;
    if (myreg.test(val)) {
      return true;
    } else {
      return false;
    }
  },
  /***身份证验证 */
  isCardVerify(card) {
    var myReg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
    if (myReg.test(card)) {
      return true;
    } else {
      return false;
    }
  },
  /***姓名验证***/
  isNameVerify(name) {
    var myReg = /^(?:[\u4e00-\u9fa5]+)(?:●[\u4e00-\u9fa5]+)*$|^[a-zA-Z0-9]+\s?[\.·\-()a-zA-Z]*[a-zA-Z]+$/;

    if (!myReg.test(name)) {
      return false;
    } else {
      return true;
    }
  },
  /***加载中***/
  openLoad() {
    Indicator.open({
      spinnerType: 'triple-bounce'
    });
  },
  closeLoad() {
    Indicator.close();
  },
  postAjax(url, data) {
    let that=this;
    return new Promise((resolve, reject) => {
      $.ajax({
        type: "POST",
        url: "https://www.yizhenyun.com.cn/yzypt" + url,
        data: data,
        success: function (res) {
          resolve(res)
        },
        error: function (err) {
          that.toast(err.msg)
        }
      })
    })
  },
  postRequest(url, data) {
    return new Promise((resolve, reject) => {
      $.ajax({
        type: "POST",
        url: 'https://www.yizhenyun.com.cn/yzypt'+url,
        data: data,
        success: function (res) {
          resolve(res)
        },
        error: function (err) {
        }
      })
    })
  },
  getRequest(url, data) {
    let that=this;
    return new Promise((resolve, reject) => {
      $.ajax({
        type: "get",
        url: "https://www.yizhenyun.com.cn/yzypt" + url,
        data: data,
        success: function (res) {
          resolve(res)
        },
        error: function (err) {
          that.toast(err.msg)
        }
      })
    })
  },
    /***时间转换***/
    timeSwitch(val, judge) {
      let date = new Date(val);
      let Month = date.getMonth() + 1;
      let day = date.getDate();
      let hours = date.getHours();
      let minutes = date.getMinutes();
      let second = date.getSeconds();
      if (judge) {
        return date.getFullYear() + '-' + Month.toString().padStart(2, '0') + '-' + day.toString().padStart(2, '0') + ' ' + hours.toString().padStart(2, '0') + ':' + minutes.toString().padStart(2, '0') + ':' + second.toString().padStart(2, '0');
      } else {
        return date.getFullYear() + '-' + Month.toString().padStart(2, '0') + '-' + day.toString().padStart(2, '0')
      }
    },

}



