import Vue from 'vue'
import Router from 'vue-router'
import Register from '@/pages/Register'

Vue.use(Router)

let router=new Router({
  routes: [
    {
      path:"/",
      redirect:"/register"
    },
    {
      path:'/register',
      name:"Register",
      meta: {
        title: '注册',
      },
      component:Register
    }
  ]
})
router.afterEach((to, from, next) => {
  window.scrollTo(0, 0);
})
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
    next()
});

export default router;



