<template>
  <div>
    <h2>注册</h2>
  <main>
      <div class="name">
        <i class="iconfont iconuser"></i>
        <input type="text" v-model.trim="name" placeholder="请输入用户名" maxlength="15">
      </div>
      <div class="name">
        <i class="iconfont icongender"></i>
        <van-radio-group v-model="sex">
          <van-radio name="男" checked-color="#0cb0b5">男</van-radio>
          <van-radio name="女" checked-color="#0cb0b5">女</van-radio>
        </van-radio-group>
      </div>
      <!-- <div class="name">
        <i class="iconfont iconriqi"></i>
        <div class="input" @click="openShow()">{{birthDay}}</div>
      </div> -->
      <div class="name">
        <i class="iconfont iconshenfenzhenghao"></i>
        <input type="text" v-model.trim="idNo" placeholder="请输入身份证号">
      </div>
      <div class="name">
        <i class="iconfont iconiconsg"></i>
        <input type="number" v-model.trim="height" placeholder="请输入身高">
      </div>
      <div class="name">
        <i class="iconfont icontizhong"></i>
        <input type="number" v-model.trim="weight" placeholder="请输入体重">
      </div>
      <div class="phone">
        <i class="iconfont iconshoujihao3"></i>
        <input
          type="number"
          pattern="[0-9]*"
          @input="inputVerify()"
          placeholder="请输入手机号"
          v-model.trim="phone"
        >
      </div>
      <div class="code">
        <i class="iconfont iconcodev"></i>
        <input type="number" placeholder="请输入验证码" v-model.trim="code">
        <span
          class="getCode"
          :class="[isActive?'active':'getCode']"
          @click="getCode()"
        >{{codeValue}}</span>
      </div>
      <!-- <van-popup v-model="dateshow" position="bottom" :overlay="true">
        <van-datetime-picker
          v-model="currentDate"
          type="date"
          :min-date="minDate"
          @click-overlay="clickOverlay"
          @cancel="cancelDate"
          @confirm="confirmBirthday"
        />
      </van-popup> -->
      <div class="button" @click="toHome()">保存</div>
    </main>
  </div>
</template>
<script>
import base from "@/assets/js/base";
export default {
  name: "register",
  data() {
    return {
      name: "",
      sex: "男",
      height: "",
      weight: "",
      idNo: "",
      birthDay: "请选择出生日期",
      currentDate: new Date(),
      phone: "",
      code: "",
      isActive: false,
      codeValue: "获取",
      dateshow: false,
      minDate: new Date(1910, 10, 1)
    };
  },
  methods: {
    inputVerify() {
      if (this.phone.length > 11) {
        this.phone = this.phone.substring(0, 11);
      }
    },
    cancelDate() {
      this.dateshow = false;
    },
    clickOverlay() {
      this.dateshow = false;
    },
    confirmBirthday(e) {
      this.birthDay = base.timeSwitch(e, false);
      this.cancelDate();
    },
    openShow() {
      this.dateshow = true;
    },
    /**获取验证码 */
    getCode() {
      if (!base.isPoneAvailable(this.phone)) {
        base.toast("手机号格式不正确");
        return;
      }
      if (this.codeValue == "获取" && this.phone) {
        this.getCodeFun();
        this.isActive = true;
        this.codeValue = 60;
        var setSecond = setInterval(() => {
          if (this.codeValue <= 0) {
            clearInterval(setSecond);
            this.codeValue = "获取";
            this.isActive = false;
          } else {
            this.codeValue--;
          }
        }, 1000);
      }
    },
    getCodeFun() {
      base
        .postRequest("/yzypt/sednSms", { phone: this.phone })
        .then(res => {
          if (res.status != 0) {
            base.toast(res.msg);
            return;
          }
        });
    },
    toHome() {
      if (this.phone && this.name && this.code && this.height && this.weight && this.idNo) {
        // if(this.birthDay=="请选择出生日期"){
        //   base.toast('请选择出生日期');
        //   return ;
        // }
        if (base.isPoneAvailable(this.phone)) {
          let data = {
            name: this.name,
            sex: this.sex,
            height: this.height,
            weight: this.weight,
            idNo: this.idNo,
            phone: this.phone,
            code: this.code
          };
          base.postRequest("/ptInfo/register", data).then(res => {
            if (res.status == 0) {
              if(location.search.split('=')[1]==1){
              location.href="https://www.yizhenyun.com.cn/yzypt/wx/zzxd/index.html#/home"
              }else{
              location.href="https://www.yizhenyun.com.cn/yzypt/wx/br/index.html#/myDoctor"
              }
           
            } else {
              base.toast(res.msg);
            }
          });
        } else {
          base.toast("手机号格式不正确");
        }
      } else {
        base.toast("请输入完整信息");
      }
    }
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
h2{
  padding-top:ptr(30px);
  font-size: ptr(16px);
  text-align: center;
  color: #0cb0b5;
  font-weight: 600;
}
main {
  padding: ptr(20px);
  box-sizing: border-box;
}
.name,
.phone,
.code {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: ptr(10px) 0;
  margin-top: ptr(10px);
  border-radius: ptr(4px);
  background-color: rgba(255, 255, 255, 1);
  .iconfont {
    color: #666;
    font-size: ptr(20px);
    padding: 0 ptr(10px);
    width: ptr(30px);
    text-align: center;
  }
  .iconshenfenzheng {
    font-size: ptr(22px);
  }
  .getCode {
    width: ptr(50px);
    height: ptr(28px);
    color: #fff;
    line-height: ptr(28px);
    border-radius: ptr(4px);
    text-align: center;
    background-color: #0cb0b5;
    margin-right: ptr(20px);
  }
  .active {
    width: ptr(50px);
    height: ptr(28px);
    color: #666;
    line-height: ptr(28px);
    border-radius: ptr(4px);
    text-align: center;
    background-color: #eee;
    margin-right: ptr(20px);
  }
  input,
  .input {
    flex: 1;
    padding: ptr(5px) ptr(10px);
    height: ptr(20px);
    line-height: ptr(20px);
    border: none;
    font-size: ptr(14px);
    background-color: transparent;
  }
  .btn {
    width: ptr(50px);
    text-align: center;
    padding: ptr(5px);
    margin-right: ptr(10px);
    height: ptr(16px);
    line-height: ptr(16px);
    color: #fff;
    border-radius: ptr(5px);
    background-color: #0cb0b5;
  }
  .btnActive {
    background-color: #999;
  }
}

.van-radio-group {
  display: flex;
  align-items: center;
  flex: 1;
}
.van-radio {
  margin-right: ptr(20px);
  font-size: ptr(14px);
}
.isRelation {
  display: flex;
  margin: ptr(10px) 0;
  justify-content: space-between;
  align-items: center;
  font-size: ptr(14px);
}
.button {
  margin: ptr(40px) 0;
  color: #fff;
  font-size: ptr(14px);
  text-align: center;
  line-height: ptr(40px);
  border-radius: ptr(4px);
  background-color: #0cb0b5;
}
</style>



